  if (document.querySelector(".carousel__inner")) {
  const carouselInner = document.querySelector(".carousel__inner");
  const carouselItems = document.querySelectorAll(".carousel__item");
  const carouselBtnPrev = document.querySelector(".carousel__prev");
  const carouselBtnNext = document.querySelector(".carousel__next");
  
  let sliderTranslate = 0;
  
  
  carouselBtnNext.addEventListener("click", () => {
    slideNext()
  })
  
  const slideNext = () => {
  
      const slideWidth = carouselItems[0].getBoundingClientRect().width;
      console.log(sliderTranslate);
      if (
        sliderTranslate <=
        -carouselInner.getBoundingClientRect().width + slideWidth
      ) {
        sliderTranslate = 0;
        carouselInner.style.transform = `translateX(${sliderTranslate}px)`;
      } else {
        console.log(sliderTranslate);
        sliderTranslate -= slideWidth;
        console.log(-carouselInner.getBoundingClientRect().width);
        carouselInner.style.transform = `translateX(${sliderTranslate}px)`;
      }
  };
  
  const slidePrev = () => {
    carouselBtnPrev.addEventListener("click", () => {
      const slideWidth = carouselItems[0].getBoundingClientRect().width;
      console.log(sliderTranslate);
      if (sliderTranslate >= 0) {
        sliderTranslate =
          -carouselInner.getBoundingClientRect().width + slideWidth;
        carouselInner.style.transform = `translateX(${sliderTranslate}px)`;
      } else {
        sliderTranslate += slideWidth;
        console.log(sliderTranslate);
        carouselInner.style.transform = `translateX(${sliderTranslate}px)`;
      }
    });
  };
  }
