if (document.querySelectorAll('.faq__button')) {
    const faqButtons = document.querySelectorAll('.faq__button')

    faqButtons.forEach(button => {
        button.addEventListener('click', () => {
            const childrens = button.children;
            const answerBlock = [...childrens].find(item => item.classList.contains('faq__answer'))
            answerBlock.classList.toggle('faq__answer--active')

            const titleBlock = [...childrens].find(item => item.classList.contains('faq__title'))
            console.log(titleBlock)
            titleBlock.classList.toggle('faq__title--opened')
        })
    })
}

