const menuItems = document.querySelectorAll('.info__menu-item'),
      menuContent = document.querySelectorAll('.info__block');
     

menuContent.forEach(item => item.style.display = "none")
menuContent[0].style.display = 'grid'

const addActive = () => {
    menuItems.forEach((elem, index) => elem.addEventListener('click', (e) => {
        if (e.currentTarget.classList.contains('info__menu-item')) {
            removeActive()
            menuContent.forEach(item => item.style.display = "none")

            menuContent[index].style.display = 'grid'
            e.currentTarget.classList.add('info__menu-item--active')
            console.log(e.currentTarget)
        }
    }))
}

const removeActive = () => {
    menuItems.forEach(item => {
        item.classList.remove('info__menu-item--active')
    })
}
addActive()