const avatarsInner = document.querySelector(".reviews__review-avatars");
const avatars = document.querySelectorAll(".reviews__avatar");

const reviewsInner = document.querySelector(".reviews__review-inner");
const reviews = document.querySelectorAll(".reviews__item");

const dots = document.querySelectorAll(".reviews__dot");

const nextSlideButton = document.querySelector(".slider__button--next");
const prevSlideButton = document.querySelector(".slider__button--prev");

// --------------------------------------------------

const avatarWidth = avatars[1].getBoundingClientRect().width;
let avatarTranslate = 0;
let counter = 0;

let offset = 0;
let reviewsItemwWidth = reviews[0].getBoundingClientRect().width;
let reviewsWidth = reviewsItemwWidth * reviews.length - 1;

// --------------------------------------------------

nextSlideButton.addEventListener("click", () => nextAvatar());
prevSlideButton.addEventListener("click", () => prevAvatar());

// --------------------------------------------------

function nextAvatar() {
  if (counter >= avatars.length - 1) return;
  avatarTranslate -= avatarWidth + 15;
  counter++;

  // avatarMove(avatarTranslate);
  contentWay(reviewsItemwWidth);

  avatarActive(counter);
  dotActive(counter);
}

function prevAvatar() {
  if (counter <= 0) return;
  avatarTranslate += avatarWidth + 15;
  counter--;

  // avatarMove(avatarTranslate);
  contentWay(-reviewsItemwWidth);

  avatarActive(counter);
  dotActive(counter);
}

function avatarMove(move) {
  avatarsInner.style.transform = `translateX(${move}px)`;
}

function contentMove(move) {
  reviewsInner.style.transform = `translateX(-${move}px)`;
}

function dotmove() {
  dots.forEach((dot, index) => {
    dot.addEventListener("click", () => {
      counter = index;
      avatarActive(counter);
      dotActive(counter);

      offset = index === 0 ? 0 : reviewsItemwWidth * index;
      reviewsInner.style.transform = `translateX(-${offset}px)`;
    });
  });
}

function contentWay(way) {
  offset += way;
  if (offset > reviewsWidth) return;
  if (offset < 0) return;
  contentMove(offset);
}

function avatarActive(index) {
  avatars.forEach((item) => item.classList.remove("reviews__avatar--active"));
  avatars[index].classList.add("reviews__avatar--active");
}

function dotActive(index) {
  dots.forEach((item) => item.classList.remove("reviews__dot--active"));
  dots[index].classList.add("reviews__dot--active");
}

// --------------------------------------------------

avatarActive(0);
dotActive(0);
dotmove();

// --------------------------------------------------
