const menuBtn = document.querySelector(".menu__burger");
const menuList = document.querySelector(".menu__list");

// --------------------------------------------------

menuBtn.addEventListener("click", () => {
  menuBtn.classList.toggle("menu__burger--open");
  document.querySelector("body").classList.toggle("blocked");

  if (menuList.classList.contains("menu__list--active")) {
    menuList.classList.remove("menu__list--active", "fade");
    return;
  }
  menuList.classList.add("menu__list--active", "fade");
});
